import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import MaterialTable from 'material-table';
import { makeStyles, withStyles } from '@material-ui/core/styles';
import { createMuiTheme, ThemeProvider } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import PeopleAltIcon from '@material-ui/icons/PeopleAlt';

import HireList from './HireList';

const theme = createMuiTheme({
  palette: {
    primary: {
      main: '#428bca',
    },
    secondary: {
      main: '#d9534f',
    },
  },
});

const useStyles = makeStyles((theme) => ({
  table: {
    fontFamily: 'Quicksand',
    fontSize: '1.25em',
    fontWeight: '650',
    boxShadow: '2px 2px 5px 2px rgba(0,0,0,0.35)',
    border: '1px #5B5B5B solid',
  },
  title: {
    fontFamily: 'Quicksand',
    fontSize: '1.45em',
    fontWeight: '650',
  },
}));

export default function HireTable({ applicant }) {
  const classes = useStyles();
  const filtered_applicant = applicant.filter((item) => item !== null);

  if (applicant.length === 0) {
    return (
      <ThemeProvider theme={theme}>
        <MaterialTable
          className={classes.table}
          title={
            <Typography
              variant="h3"
              color="primary"
              className={classes.title}
              gutterBottom
            >
              <PeopleAltIcon color="primary" /> No one haven't applied for this
              job
            </Typography>
          }
        />
      </ThemeProvider>
    );
  }

  return (
    <ThemeProvider theme={theme}>
      <MaterialTable
        className={classes.table}
        title={
          <Typography
            variant="h3"
            color="primary"
            className={classes.title}
            gutterBottom
          >
            <PeopleAltIcon color="primary" /> Applicants Information
          </Typography>
        }
        columns={[
          {
            field: 'url',
            title: 'Avatar',
            render: (rowData) => (
              <img
                src={`https://avatars.dicebear.com/api/avataaars/${rowData.email}.svg`}
                style={{ width: 50, borderRadius: '50%' }}
              />
            ),
            headerStyle: {
              color: '#428bca',
              fontFamily: 'Quicksand',
              fontSize: '1.15em',
              fontWeight: '700',
            },
          },
          {
            title: 'Name',
            field: 'name',
            cellStyle: {
              fontFamily: 'Quicksand',
              fontSize: '0.9em',
              fontWeight: '650',
            },
            headerStyle: {
              color: '#428bca',
              fontFamily: 'Quicksand',
              fontSize: '1.15em',
              fontWeight: '700',
            },
          },
          {
            title: 'Email',
            field: 'email',
            cellStyle: {
              fontFamily: 'Quicksand',
              fontSize: '0.9em',
              fontWeight: '650',
            },
            headerStyle: {
              color: '#428bca',
              fontFamily: 'Quicksand',
              fontSize: '1.15em',
              fontWeight: '700',
            },
          },
        ]}
        data={filtered_applicant}
        detailPanel={[
          {
            icon: 'arrow_forward_ios',
            openIcon: 'call_received',
            tooltip: 'Show Both',
            render: (rowData) => {
              return (
                <div
                  style={{
                    fontSize: '1.15em',
                    fontFamily: 'Quicksand',
                    textAlign: 'left',
                    color: '#111',
                    padding: '1em',
                  }}
                >
                  <HireList
                    phone={rowData.phone}
                    email={rowData.email}
                    company={rowData.company}
                    cv={rowData.cv}
                  />
                </div>
              );
            },
          },
        ]}
      />
    </ThemeProvider>
  );
}
